/* Style your components */
.NavbarItems{
    background-color: rgb(198, 249, 205);
 display: flex;
 justify-content: space-between;
 align-items: center;
 padding: 0 30px;
 box-shadow: 0 5px 15px 0 rgba(0,0,0,.25);
 width: 95%;
 height: 80px;
 border-radius: 13px;
 position: fixed;
 top: 20px;
 left: 50%;
 transform: translate(-50%);
 z-index: 9999;
}
.navbar-logo{
    color: #222;
    font-size: 2rem;
    cursor: pointer;
}
.nav-menu{
    display: grid;
    grid-template-columns: repeat(6,auto) ;
    grid-gap:10px;
    list-style: none;
    align-items: center;
}
.nav-links{
    text-decoration: none;
    color: #222;
    font-size: 1.2rem;
    font-weight: 600;
    padding: 0.7rem 1rem;
    white-space:nowrap;
}

.nav-links i {
    padding-right: 10px;
}

.nav-links:hover{
    background-color: #65ce03;
    color:azure;
    border-radius: 4px;
    transition: all 0.2s ease-in-out;
}

.nav-links-mobile{
    display: none;
}
button{
    padding: 0.5rem 1rem;
    white-space: nowrap;
    border-radius: 0.3rem;
    font-size: 1rem;
    font-weight: 600;
    border: none;
    cursor: pointer;
    transition: 0.2s ease-in-out;
}
button:hover{
    background-color: black;
    color: azure;
}

.menu-icons{
    display: none;
}

@media screen and (max-width:850px) {
    .NavbarItems{
        z-index: 99;
    }
    .nav-menu{
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        background-color: #fdfdfd;
        width: 100%;
        height: auto;
        position: absolute;
        top: 0;
        left: -100%;
        opacity: 0;
        align-items: stretch;
        margin: 0;
        padding: 80px 0 30px 0;
        transition: 0.3s ease-in-out;
        border-radius: 13px;
       

    }

    .nav-menu.active{
        left: 0;
        opacity: 1;
        z-index: -1;
        transition: 0.3s ease-in-out;
    }

    .nav-links{
        display: block;
        width: 100%;
        font-size: 1.2rem;
        padding: 2rem;
    }

    .nav-links:hover{
        background-color: rgb(2, 243, 58);
        transition: none;
    }

    button{
display: none;
    }
    .nav-links-mobile{
        display: block;
        text-align: center;
        padding: 1.5rem;
        margin: auto;
        border-radius: 4px;
        width: 80%;
        background: #003e02;
        text-decoration: none;
        color: azure;
        font-size: 1.2rem;
        font-weight: 700;
        letter-spacing: 2px;
    }

    .menu-icons{
        display: block;
        cursor: pointer;
    }

    .menu-icons i{
        font-size: 1.2rem;
        color: black;
    }
}